<template>
  <Page
    v-model:isRefresh="isRefresh"
    :title="$t('vote.myVote')"
    hideHeader
    :openRefresh="true"
    :onRefresh="onRefresh"
    :onClickLeft="onClickLeft"
    bodyClass="bg-white"
  >
    <van-list
      v-model:loading="loading"
      class="w-100b f-1"
      :finished="finished"
      :finished-text="$t('form.noMoreData')"
      :loading-text="$t('form.loading')"
      @refresh="onRefresh"
      @load="onLoad"
    >
      <Column v-for="(row) in list" :key="row" class="w-100b p-30 p-b-0">
        <Row class="w-100b m-b-20" x="sb" y="s">
          <Row>
            <img class="w-40 h-40 m-r-10" :src="row.iconUrl" alt="">
            <span class="label-lg bold">{{ row.coinName }}</span>
          </Row>
          <span class="label-sm c-info">{{ dayjs(row.recordTime).format('YYYY-MM-DD HH:mm:ss') }}</span>
        </Row>
        <Row class="w-100b label-sm  m-b-10" x="sb" y="s">
          <span class="c-info">{{ $t('vote.inTheEnd') }}</span>
          <span class="bold">{{ voteResultOptions.find(e => row.voteResult == e.value)?.label }}</span>
        </Row>
        <Row class="w-100b label-sm  m-b-10" x="sb" y="s">
          <span class="c-info">{{ $t('table.number') }}</span>
          <span class="bold">
            <Num>{{ row.voteAmount }}</Num>
            <span class="m-l-4">{{ row.type != 2 ? 'BFT' : row.coinName }} </span>
          </span>
        </Row>
        <Row class="w-100b label-sm " x="sb" y="s">
          <span class="c-info">{{ $t('public.type') }}</span>
          <span class="bold">{{ typetOptions.find(e => row.type == e.value)?.label }}</span>
        </Row>
        <div class="w-100b border-b m-t-30" />
      </Column>
    </van-list>
  </Page>
</template>

<script setup>
import { VoteList, FinishedList, DiaCoinApplication } from '@/view/vote/components'

const languageStore = useLanguageStore()
const route = useRoute()

const onClickLeft = () => {
  router.go(-1)
}

let loading = $ref(false)
let isRefresh = $ref(false)
let list = $ref([])
let total = $ref()

const pages = reactive({
  ...PAGES,
  // pageNum: 1,
  // pageSize: 1000,
})

const voteResultOptions = $computed(() => [
  { label: $t('public.success'), value: 1 },
  { label: $t('public.failure'), value: 0 },
])

const typetOptions = $computed(() => [
  { label: $t('vote.typeMap.0'), value: 0 },
  { label: $t('vote.typeMap.1'), value: 1 },
  { label: $t('vote.typeMap.2'), value: 2 },
  { label: $t('vote.typeMap.3'), value: 3 },
])

const finished = $computed(() => {
  if (loading == true) {
    return true
  } else if (list?.length == total) {
    return true
  } else {
    return false
  }
})

const getData = async () => {
  try {
    loading = true
    const body = {
      activityId: route.query.activityId,
      ...pages,
    }
    const d = await voteApi.history(body)
    const ls = d.records?.map((e, i) => ({ ...e, index: i + 1 })) ?? []
    if (isArray(list)) {
      list.push(...ls)
    } else {
      list = ls
    }
    total = d.total
  } finally {
    loading = false
  }
}

const onRefresh = async () => {
  try {
    isRefresh = true
    pages.pageNum = 1
    const body = {
      activityId: route.query.activityId,
      ...pages,
    }
    const d = await voteApi.history(body)
    list = d.records?.map((e, i) => ({ ...e, index: i + 1 })) ?? []
    total = d.total
  } finally {
    isRefresh = false
  }
}

const onLoad = () => {
  pages.pageNum = pages.pageNum + 1
  getData()
}
// winMes.postParent('hiddenNaviBar')

onBeforeMount(() => {
  onRefresh()
})

onMounted(() => {})
</script>

